  text-wrap: balance;
  line-height: 130%;
  /* 104px */
  letter-spacing: -0.8px;
  text-transform: capitalize;
}

.header__content p {
  margin: 0 auto;
  max-width: 606px;
  opacity: 0.8;
  font-size: clamp(16px, 13.02857px + 0.00762 * 100vw, 24px);
  line-height: 160%;
  /* 38.4px */
  text-wrap: balance;
}

.header__search {
  display: inline-block;
  margin: 45px auto 0;
}

@media (max-width: 819px) {
  .header__search {
    display: none;
    /* TEST */
  }
}

/* Dots */
.header__dot {
  position: absolute;
}

@media (max-width: 819px) {
  .header__dot {
    display: none;
  }
}

.header__dot--sofa {
  left: calc(296 * 100% / 1440);
  top: calc(685 * 100% / 1084);
  transform: translate(0%, 0%);
}

.header__dot--vase {
  left: calc(114 * 100% / 1440);
  top: calc(754 * 100% / 1084);
  transform: translate(-50%, -50%);
}

.header__dot--item {
  left: calc(810 * 100% / 1440);
  top: calc(787 * 100% / 1084);
  transform: translate(-50%, -50%);
}

.header__dot--wall {
  right: calc(46 * 100% / 1440);
  top: calc(821 * 100% / 1084);
}

.logo {
  font-weight: 700;
  font-size: clamp(18px, 14.28571px + 0.00952 * 100vw, 28px);
  letter-spacing: 0.28px;
  text-transform: capitalize;
  color: #fff !important;
}

.nav {
  display: grid;
  grid-template-columns: 2fr 8fr 2fr;
  gap: 20px;
}

.nav__logo {
  align-self: center;
}

.nav__btn {
  display: none;
}

@media (max-width: 992px) {
  .nav__btn {
    display: block;
  }
}

.nav-list {
  align-self: center;
  justify-self: center;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 65px;
  font-weight: 500;
  font-size: 18px;
}

@media (max-width: 992px) {
  .nav-list {
    display: none;
  }
}

.nav-list__item {
  position: relative;
  padding: 10px 0;
}

.nav-list__link {
  transition: opacity 0.1s ease-in;
}

.nav-list__link:hover {
  opacity: 0.7;
}

.nav-list__link--dropdown {
  display: flex;
  align-items: center;
  gap: 8px;
}

.nav-list__link--dropdown::after {
  content: url("./../img/icons/arrow-down.svg");
  height: 18px;
}

.nav-list__sub-nav {
  opacity: 0;
  width: 160px;
  pointer-events: none;
  position: absolute;
  left: 0;
  top: calc(100%);
  transform: translate(0%, 10%);
  transition: all 0.2s ease-in;
}

.nav-list__item:hover .nav-list__sub-nav {
  pointer-events: all;
  opacity: 1;
  transform: translate(0%, 0%);
}

.sub-nav {
  display: grid;
  gap: 16px;
  padding: 20px;
  border-radius: 16px;
  background-color: var(--footer-bg);
  color: var(--footer-text-color);
  font-size: 16px;
}

.sub-nav a {
  color: var(--footer-text-color);
  transition: color 0.1s ease-in;
}

.sub-nav a:hover {
  color: var(--accent);
}

.cart {
  align-self: center;
  justify-self: end;
  position: relative;
}

.cart__count {
  position: absolute;
  top: 3px;
  right: -6px;
  display: inline-block;
  height: 25px;
  padding: 0 6px;
  border-radius: 40px;
  background-color: var(--accent);
  font-weight: 700;
  line-height: 25px;
  font-family: var(--font-cards);
  font-size: 16px;
}

.search-form {
  display: flex;
  gap: 10px;
  max-width: 344px;
  padding: 8px 8px 8px 20px;
  justify-content: space-between;
  align-items: center;
  height: 56px;
  border-radius: 42px;
  border: 0.862px solid rgba(255, 255, 255, 0.6);
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(4px);
}

.search-form__input {
  flex-grow: 1;
  color: #fff;
  font-size: 18px;
  font-weight: 500;
  line-height: 150%;
  /* 27px */
}

.search-form__input::placeholder {
  color: #fff;
  font-weight: 400;
}

.search-form__btn {
  flex-shrink: 0;
  width: 40px;
  height: 40px;
  border-radius: 24px;
  background: var(--accent);
}

.info-hint {
  position: absolute;
  top: -26px;
  left: 50%;
  transform: translate(-50%, -100%);
  display: flex;
  gap: 8px;
  justify-content: center;
  padding-top: 12px;
  width: 112px;
  height: 56px;
  background-image: url("./../img/icons/hint.svg");
}

.info-hint__radio {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

.info-hint__radio:checked + .info-hint__radio-fake {
  background-image: url("./../img/icons/check-no-shadow.svg");
  background-repeat: no-repeat;
  background-position: center center;
}

.info-hint__radio-fake {
  flex-shrink: 0;
  width: 24px;
  height: 24px;
  background-color: gray;
  border: 2px solid #fff;
  border-radius: 50%;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
}

.info-hint__radio-fake--orange {
  background-color: var(--ui-orange);
}

.info-hint__radio-fake--biruza {
  background-color: var(--ui-biruza);
}

.info-hint__radio-fake--gray {
  background-color: var(--ui-gray);
}

