$breakpoints: (
  xxl: 1920px,
  xl: 1440px,
  lg: 1280px,
  md: 1024px,
  md-s: 992px,
  sm: 768px,
  xs: 512px,
  xxs: 390px
) !default;

:root {
  @each $name, $breakpoint in $breakpoints {
    #{--breakpoint-}#{$name}: $breakpoint;
  }
}

@function get-media($breakpoint) {
  @return map-get($map: $breakpoints, $key: $breakpoint);
}
